html {
    box-sizing: border-box;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    line-height: 1.5;
    font-family: Roboto, sans-serif;
    font-size: 16px;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    cursor: default;
}

a {
    color: inherit;
    background: transparent;
    text-decoration: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);

    &:hover,
    &:active,
    &:focus {
        text-decoration: none;
    }

    &:focus {
        outline: none;
    }
}

h1,
h2,
h3 {
    font-weight: 600;
}

h1 {
    font-size: 60px;
}

h2 {
    font-size: 35px;
}

h3 {
    font-size: 30px;
}

ul {
    margin: 0;
    padding: 0;
}

li {
    list-style: none;
}

a,
button {
    outline: none;
}
